.o-wrapped-icon{
	position: relative;

	&,
	&__wrapper svg{
		height: 90px;
		width: 90px;
	}

	&__wrapper{
		transition: fill $userOut $easeOut;
	}

	&--large,
	&--large svg{
		height: 110px;
		width: 110px;
	}

	&--orange-wrap &__wrapper{
		fill: $primary;
	}

	&--white &__wrapper{
		fill: #fff;
	}

	&__icon{
		position: absolute;
		top: 50%;
		left: 50%;
		width: 55px;
		height: 55px;
		transform: translate(-50%, -50%);
		fill: $black;

		&,
		& svg{
			width: 55px;
			height: 55px;
		}

		&--stroke{
			fill: none;
			stroke: $black;
		}
	}

	&--large &__icon,
	&--large &__icon svg{
		width: 60px;
		height: 60px;
	}

	&--white &__icon{
		fill: #fff;
	}

	&--white &__icon--stroke{
		fill: none;
		stroke: #fff;
	}
}
