.l-angle-content{
	position: relative;
	padding-left: $gutter;
	padding-right: $gutter;
	width: 100%;
	min-height: 310px;
	padding-top: 50px;

	@media (min-width: $sm){
		width: 50%;
		padding-right: 0;
		padding-top: 80px;
	}

	&--offset{
		width: 100%;

		@media (min-width: $sm){
			margin-left: $full-gutter + 69px;
			width: 40%;
		}
	}

	.o-btn{
		margin-top: 25px;
	}

	&__angle{
		position: relative;
		height: 0;
		padding-bottom: ratio(248, 213);
		transform: translate(calc(-100% + 32px), -101%);
		transition: transform 1000ms 250ms $easeOut;

		&-container{
			position: absolute;
			width: 250px;
			top: 80px;
			right: 0;
			transform: translateX(95%);
			overflow: hidden;
		}

		@media (min-width: $sm){
			&:before{
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				width: 30px;
				height: 100%;
				background-color: $black;
				transform: skew(45deg) translateX(110px);
			}

			&--green{
				&:before{
					background-color: $tertiary;
				}
			}

			&--blue{
				&:before{
					background-color: $secondary;
				}
			}
		}
	}

	.active &__angle{
		transform: none;
	}

	&__inline{
		position: relative;

		&-container{
			position: absolute;
			top: 50px;
			left: 55px;
			width: 215px;
			height: 100%;
			z-index: 5;
			overflow: hidden;
		}
	}

	&__inline &__angle:before{
		transform: skew(45deg) translateX(90px);
	}
}
