.c-support-form{
	margin-top: 65px;

	&__question{
		margin-bottom: 55px;
		pointer-events: none;
		width: 100%;

		> *{
			opacity: 0.3;
			transition: opacity $userOut $easeOut;
		}

		&:last-child{
			padding-top: 65px;
			padding-bottom: 55px;
			margin-top: 0;
			margin-bottom: 0;
		}

		&:first-child > *,
		&.active > *{
			opacity: 1;
			pointer-events: auto;
		}
	}
}
