.o-accordion{
	&__list{
		> *{
			&:not(:last-child){
				margin-bottom: 18px;
			}
		}
	}

	&__btn{
		position: relative;
		width: 100%;
		border-color: $black;
		background-color: transparent;
		color: $black;
		text-align: left;

		&:after,
		&:before{
			content: '';
			position: absolute;
			top: 50%;
			right: 12px;
			transform: translateY(-50%);
			background-color: currentColor;
		}

		&:after{
			width: 2px;
			height: 18px;
			right: 20px;
		}

		&:before{
			height: 2px;
			width: 18px;
		}

		&:hover,
		&:focus,
		.active &{
			background-color: $tertiary;
			border-color: $tertiary;
			color: #fff;
		}
	}

	&__container{
		height: 0;
		will-change: height;
		overflow: hidden;
		transition: height $userInP1 $easeOut;
	}

	.active &__container{
		height: auto;
		transition: height $userOutP1 $easeOut;
	}

	&__content{
		padding: space(3) space(2) space(1);
	}
}
