.c-header{
	display: flex;
	position: relative;
	height: 45vh;
	height: 45vmin;
	min-height: 300px;
	color: #fff;
	background-color: $primary;

	&__title{
		position: relative;

		h1{
			font-size: 8.4rem;
			margin-bottom: 0;

			@include css-lock(4, 8.4, 500, 1300);
		}
	}

	&__container{
		position: relative;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		justify-content: center;
		height: 100%;
		width: 100%;
	}

	&__logo{
		position: absolute;
		top: 70px;
		left: $gutter;

		@media (min-width: $md){
			top: 40px;
		}

		svg{
			height: 48px;
			width: 215px;
		}
	}

	&__nav{
		position: absolute;
		bottom: 0;
		left: 0;
		transform: translateY(100%);

		@media (min-width: $md){
			transform: translateY(75%);
		}

		> *{
			width: auto;
		}

		select{
			padding: 15px 22px;
			border: 2px solid #fff;
			border-radius: 10px;
			background-color: $primary;
			color: #fff;

			&:focus{
				background-color: $primary;
			}
		}
	}
}
