.o-hamburger{
	position: relative;
	height: 22px;
	width: 30px;

	&:after,
	&:before{
		content: '';
	}

	span,
	&:after,
	&:before{
		display: block;
		width: 100%;
		height: 3px;
		background-color: currentColor;
		position: absolute;
		left: 0;
	}

	span{
		top: 50%;
		transform: translateY(-50%);
	}

	&:before{
		top: 0;
	}

	&:after{
		bottom: 0;
	}
}
