.c-footer{
	display: flex;
	flex-direction: column;
	padding-top: 70px;
	padding-bottom: 60px;
	align-items: center;

	@media (min-width: $lg){
		padding-top: 80px;
		flex-direction: row;
		justify-content: space-around;
		align-items: flex-start;
	}

	> *{
		flex: 1 1 auto;
	}

	a{
		color: $primary;

		&:hover,
		&:focus{
			color: $secondary;
		}
	}

	&__logo,
	&__escape-logo{
		display: flex;
		justify-content: center;
	}

	&__logo{
		margin-bottom: 60px;

		@media (min-width: $lg){
			margin-bottom: 0;
		}

		svg{
			height: 62px;
			width: 276px;
		}
	}

	&__escape-logo{
		@media (min-width: $sm){
			margin-top: 14px;
		}

		svg{
			width: 40px;
			height: 40px;
		}
	}

	&__container{
		flex: 1 1 auto;
		max-width: 750px;
		margin-left: $gutter;
		margin-right: $gutter;
		margin-bottom: 40px;
		display: flex;
		flex-direction: column;

		@media (min-width: $lg){
			flex-basis: 750px;
			margin: 0;
		}
	}

	&__content{
		margin-top: space(2);
		display: flex;
		flex-direction: column;
		align-items: center;

		@media (min-width: $sm){
			margin-top: space(5);
			flex-direction: row;
			justify-content: space-between;
			align-items: stretch;
		}

		> *:not(:last-child){
			margin-right: space(2);
		}
	}

	&__contact{
		display: flex;
		flex-direction: column;
	}

	&__contact-links,
	&__location{
		display: flex;
		flex-direction: column;
		align-items: center;
		text-align: center;

		@media (min-width: $sm){
			flex-direction: row;
		}

		> *:not(:last-child){
			margin-right: space(1);
		}
	}

	&__contact-links{
		font-size: 1.8rem;
		font-weight: 600;

		a{
			display: block;

			@media (min-width: $sm){
				display: inline;
			}
		}

		> *{
			margin-bottom: space(4);

			@media (min-width: $sm){
				margin-bottom: 6px;
			}
		}
	}

	&__nav{
		text-align: center;

		@media (min-width: $sm){
			display: flex;
			text-align: left;

			> *:not(:first-child){
				position: relative;
				margin-left: 18px;

				&:before{
					content: '•';
					position: absolute;
					top: 50%;
					left: -9px;
					transform: translate(-50%, -50%);
				}
			}
		}

		a{
			text-decoration: none;
			color: $text-color;

			&:hover,
			&:focus{
				text-decoration: underline;
			}
		}
	}

	&__social{
		@media (max-width: $xs-max){
			display: none;
		}
	}
}
