$darkarea: rgba(0, 0, 0, 0.3);
$slidein: #fff;

.c-slidein{
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	overflow: hidden;
	pointer-events: none;
	z-index: $nav-index;

	&.visible{
		pointer-events: auto;
	}

	&:before{
		content: '';
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		background-color: $darkarea;
		opacity: 0;
		transition: opacity $userOut $easeOut;
	}

	&.visible:before{
		opacity: 1;
	}

	&__container{
		position: absolute;
		top: 0;
		right: 0;
		width: 100%;
		height: 100%;
		padding-left: $gutter;
		padding-right: $gutter;
		padding-top: 120px;
		background-color: $slidein;
		transform: translateX(100%);
		z-index: $nav-index + 1;

		@media (min-width: $sm){
			max-width: 750px;
			width: 65vw;
		}

		@media (min-width: $md){
			padding-left: 98px;
			padding-right: 80px;
			padding-top: 90px;
		}
	}

	&.active &__container{
		transform: none;
	}

	&.animatable &__container{
		transition: transform $userOut $easeOut;
	}

	&.animatable.visible &__container{
		transition: transform $userOutP2 $easeOut;
	}

	&__close{
		position: absolute;
		right: 12px;
		top: 54px;
		width: 48px;
		height: 48px;
		color: #000;

		@media (min-width: $md){
			right: 80px;
			top: 30px;
		}

		&:hover,
		&:focus{
			color: $primary;
		}

		> *{
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
	}
}
