.c-continue-reading{
	padding-top: $gutter;

	&__btn{
		text-transform: uppercase;
		font-size: 1.2rem;
		font-weight: 700;

		&-container{
			display: flex;
		}
	}

	&__icon{
		margin-left: 14px;
		transition: transform $userInP1 $easeOut;

		svg{
			stroke: currentColor;
			width: 1em;
			height: 1em;
		}
	}

	&.active &__icon{
		transform: rotate(180deg);
		transform-origin: center;
		transition: transform $userOutP1 $easeOut;
	}

	&__content{
		padding-top: $gutter;
	}
}
