.c-slidein-btns{
	position: absolute;
	top: 10px;
	right: 10px;
	left: 10px;
	display: flex;
	flex-direction: row-reverse;
	z-index: $nav-btns;

	> *{
		&:first-child{
			margin-left: auto;
		}
	}

	@media (min-width: $md){
		flex-direction: column;
		top: 30px;
		left: auto;

		> *{
			&:first-child{
				margin-left: 0;
			}
		}
	}

	&__btn{
		color: #fff;
		width: 48px;
		height: 48px;
		position: relative;

		> *{
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}

		&:hover,
		&:focus{
			color: #000;
		}
	}

	&.slidein-visible &__btn{
		color: #000;

		&:hover,
		&:focus,
		&.active{
			color: $primary;
		}
	}

	svg{
		fill: currentColor;
		width: 31px;
		height: 31px;
	}
}
