.c-image-intro{
	padding-top: 60px;
	padding-bottom: 20px;
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	text-align: center;

	@media (min-width: $md){
		padding-top: 115px;
	}

	&__image{
		transform: translateX(-19%);
		margin-bottom: 80px;
	}

	&__title{
		text-transform: uppercase;
		font-size: 4rem;
		line-height: 1.4;
		margin-bottom: 0;

		@include css-lock(2.8, 4);
	}

	&__subtitle{
		font-size: 3rem;
		color: $grey-light;
		margin-bottom: 50px;

		@include css-lock(2.2, 3);
	}
}
