.l-cta{
	padding: 45px $gutter 50px;
	display: flex;
	flex-direction: column;
	align-items: center;
	text-align: center;

	> * {
		width: 100%;
	}

	@media (min-width: $md){
		padding: 90px 10% 100px;
	}

	@media (min-width: $lg){
		padding: 90px 150px 100px;
	}

	> .o-btn:last-child{
		margin-top: space(6);
	}
}
