.c-testimonail-slider{
	&__controls-wrap{
		position: absolute;
		top: 0;
		left: 50%;
		width: 100%;
		height: 100%;
		max-width: $max-width;
		transform: translateX(-50%);
		pointer-events: none;

		> *{
			pointer-events: auto;
		}
	}

	&__arrow{
		position: absolute;
		top: 50%;
		height: 60%;
		transform: translateY(-50%);
		display: none;

		@media (min-width: $md){
			display: block;
		}

		svg{
			width: 26px;
			height: 26px;
			stroke: #fff;
		}

		&--prev{
			left: $pull-in-gutter - 60px;
		}

		&--next{
			right: $pull-in-gutter - 60px;
		}
	}

	&__dots{
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
		bottom: 10px;

		@media (min-width: $md){
			bottom: $pull-in-gutter;
			left: calc(33.333% + #{$gutter * 3});
			transform: none;
		}

		> *{
			&:not(:last-child){
				margin-right: 4px;
			}
		}
	}

	&__dot{
		color: transparent;
		border: 2px solid #fff;
		width: 12px;
		height: 12px;
		border-radius: 50%;
		transition: background-color $userOut $easeOut;

		&.active{
			background-color: #fff;
		}
	}
}
