.l-client-grid{
	display: flex;
	flex-wrap: wrap;
	justify-content: space-around;
	margin-top: 25px;
	width: 100%;

	@media (min-width: $md){
		margin-top: 50px;
	}

	> *{
		flex: 1 0 100%;
		width: 100%;
		display: flex;
		justify-content: center;
		margin-top: 50px;

		@media (min-width: $xs){
			flex: 1 0 50%;
			width: 50%;
		}

		@media (min-width: $sm){
			flex: 1 0 33.333%;
			width: 33.333%;
		}

		@media (min-width: $md){
			flex: 1 0 25%;
			width: 25%;
		}
	}
}

@supports (display: grid){
	.l-client-grid{
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		grid-gap: 30px;
		justify-items: center;
		margin-top: 50px;
		padding-left: $gutter;
		padding-right: $gutter;

		@media (min-width: $sm){
			grid-template-columns: repeat(3, 1fr);
		}

		@media (min-width: $md){
			grid-template-columns: repeat(4, 1fr);
			grid-gap: 50px;
			margin-top: 100px;
		}

		> *{
			margin-top: 0;
			width: auto;
		}
	}
}
