.c-service-row{
	width: 100%;
	margin-top: 30px;

	@media (min-width: $md){
		width: unit(100 / $columns * 11, %);
	}

	&__title{
		margin-bottom: 30px;

		> *{
			color: $grey-light;
			margin-bottom: 0;
		}
	}

	&__content-bar{
		display: inline-block;
		margin-top: 30px;
		padding: 8px 65px;
		text-align: center;
		background-color: $secondary-light;
		font-size: 1.6rem;
	}
}
