.l-angle-section{
	position: relative;
	height: 0;
	overflow: visible;
	z-index: 1;

	& + *,
	& + * + *{
		display: flex;
		flex-direction: column;
		align-items: flex-start;

		> *{
			z-index: 1;
		}
	}

	&__container{
		position: absolute;
		top: 0;
		left: 0;
		overflow: hidden;
		height: 76vh;
		width: 100vw;
	}

	&__cutout{
		width: 100%;
		height: 0;
		padding-bottom: ratio(1300, 570);
		position: relative;

		&:before,
		&:after{
			content: '';
			position: absolute;
			bottom: 0;
			background-color: #fff;
		}

		&:after{
			width: 100%;
			height: 104%;
			right: 0;

			@media (min-width: $sm){
				transform: skew(45deg);

				@include css-lock(120, 550, 992, 2560, right, px);
			}
		}

		&:before{
			width: 25%;
			height: 25%;
			left: 0;
		}

		&:before{
			content: '';
			position: absolute;
			bottom: 0;
			background-color: #fff;
		}
	}
}
