.c-primary-nav{
	margin-bottom: 66px;
	position: relative;

	ul{
		width: 50%;
	}

	a{
		display: block;
		font-size: 2.4rem;
		text-transform: uppercase;
		text-decoration: none;
		padding: 5px 0;

		&:hover,
		&:focus{
			color: $primary;
		}
	}

	li{
		ul{
			opacity: 0;
			pointer-events: none;
			display: none;
			position: absolute;
			top: 0;
			left: 50%;
			transition: opacity $userOut $easeOut;

			a{
				font-size: 1.6rem;
				text-transform: none;
			}
		}

		&.active{
			> a{
				color: $primary;
				font-weight: 700;
			}

			ul{
				opacity: 1;
				display: none;
				pointer-events: auto;

				@media (min-width: $md){
					display: block;
				}
			}
		}
	}
}
