.l-pullout{
	&--two-lists,
	&--list{
		display: flex;

		> *{
			margin-left: $gutter;
			margin-right: $gutter;
		}
	}

	&--two-lists &__content{
		flex: 1 1 60%;
		flex-basis: calc(60% - #{$full-gutter});
	}

	&--two-lists &__lists{
		display: flex;
		flex: 1 1 40%;
		flex-basis: calc(40% - #{$full-gutter});

		> *{
			&:first-child{
				margin-right: $gutter;
			}

			&:last-child:not(:first-child){
				margin-left: $gutter;
			}
		}
	}

	&--list &__content{
		flex: 1 1 66.666%;
		flex-basis: calc(66.666% - #{$full-gutter});
	}

	&--list &__lists{
		flex: 1 1 33.333%;
		flex-basis: calc(33.333% - #{$full-gutter});
	}
}

@supports (display: grid){
	.l-pullout{
		&--two-lists,
		&--list{
			display: grid;
			grid-template-columns: 1fr;
			grid-template-areas: "content" "lists";
			grid-gap: $full-gutter;
			padding-left: $gutter;
			padding-right: $gutter;

			> *{
				margin: 0;
			}
		}

		&--two-lists{
			@media (min-width: $md){
				grid-template-columns: 3fr 2fr;
				grid-template-areas: "content lists";
			}
		}

		&--list{
			grid-template-areas: "lists" "content";

			@media (min-width: $md){
				grid-template-columns: 1fr 2fr;
				grid-template-areas: "lists content";
			}
		}

		&__content{
			grid-area: content;
		}

		&__lists{
			grid-area: lists;
		}
	}
}
