.o-btn{
	display: inline-block;
	min-width: 210px;
	padding: 10px;
	font-size: 1.2rem;
	font-weight: 700;
	text-transform: uppercase;
	text-align: center;
	text-decoration: none;
	color: $black;
	background-color: $primary;
	border: $border-size solid $primary;
	transition: background-color $userOut $easeOut, color $userOut $easeOut, border-color $userOut $easeOut;
	border-radius: $border-radius;

	> *{
		pointer-events: none;
	}

	&:hover,
	&:focus{
		text-decoration: none;
		background-color: transparent;
		color: $black;
	}

	#{$typegraphyClass} &{
		margin-top: 40px;
	}

	&--ghost{
		background-color: transparent;

		&:hover,
		&:focus{
			background-color: $primary;
		}
	}

	&--black{
		background-color: $black;
		border-color: $black;
		color: #fff;

		&:hover,
		&:focus{
			color: $black;
			background-color: transparent;
		}
	}

	&--black-ghost{
		background-color: transparent;
		border-color: $black;
		color: $black;

		&:hover,
		&:focus{
			color: #fff;
			background-color: $black;
		}
	}

	&--green{
		background-color: $tertiary;
		border-color: $tertiary;
		color: #fff;

		&:hover,
		&:focus{
			color: $tertiary;
			background-color: transparent;
		}
	}

	&--green-ghost{
		background-color: transparent;
		border-color: $tertiary;
		color: $tertiary;

		&:hover,
		&:focus{
			color: #fff;
			background-color: $tertiary;
		}
	}

	&--white{
		background-color: #fff;
		border-color: #fff;
		color: $black;

		&:hover,
		&:focus{
			color: #fff;
			background-color: transparent;
		}
	}

	&--white-ghost{
		background-color: transparent;
		border-color: #fff;
		color: #fff;

		&:hover,
		&:focus{
			color: $black;
			background-color: #fff;
		}
	}

	&--white-yellow{
		background-color: #fff;
		border-color: #fff;
		color: $primary;

		&:hover,
		&:focus{
			color: #fff;
			background-color: transparent;
		}
	}

	&--white-yellow-ghost{
		background-color: transparent;
		border-color: #fff;
		color: #fff;

		&:hover,
		&:focus{
			color: $primary;
			background-color: #fff;
		}
	}
}
