.c-laptop-slider{
	position: relative;
	color: #fff;
	margin-bottom: 115px;

	&__container{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		transform: translateY(16%);
	}

	&__viewport{
		position: absolute;
		top: 50%;
		left: 0;
		width: 100%;
		overflow: hidden;
		transform: translateY(-50%);
	}

	&__track{
		display: flex;

		> *{
			flex: 1 0 100%;
			width: 100%;
		}
	}

	&__slide{
		text-align: center;
		font-size: 2.2rem;

		@include css-lock(1.8, 2.2, 560, 1200);
	}

	&__title{
		font-size: 4rem;
		margin-bottom: 0.25em;

		@include css-lock(2.8, 4, 560, 1200);
	}

	&__background{
		position: relative;
		width: 100%;
		background-color: $tertiary;

		&:after{
			content: '';
			position: absolute;
			max-width: $max-width - $full-gutter;
			width: 100%;
			height: 100%;
			background-image: url(/images/laptop-slider.png);
			background-position: bottom;
			background-repeat: no-repeat;
			background-size: contain;
			top: 0;
			left: 50%;
			transform: translate(-50%, 24%);
		}

		&-sizer{
			max-width: $max-width;
			width: 100%;
			margin: 0 auto;

			&:after{
				content: '';
				display: block;
				width: 100%;
				padding-bottom: ratio(1012, 596);
			}
		}
	}

	&__angle{
		position: absolute;
		top: 50%;
		left: 50%;
		max-width: $max-width;
		width: 100%;
		height: 100%;
		z-index: 5;
		pointer-events: none;
		display: none;

		@media (min-width: $md){
			display: block;
		}

		> *{
			position: absolute;
			top: -10%;
			left: -50%;
			width: 350px;
		}

		.l-angle-content__angle:before{
			transform: skew(45deg) translateX(160px);
		}
	}

	&__dots{
		position: absolute;
		right: 18px;
		top: 28%;
		transform: translateY(-50%);
		display: flex;
		flex-direction: column;
	}

	&__dot{
		color: transparent;
		border: 2px solid #fff;
		width: 12px;
		height: 12px;
		border-radius: 50%;
		transition: background-color $userOut $easeOut;

		&:not(:last-child){
			margin-bottom: 5px;
		}

		&.active{
			background-color: #fff;
		}
	}
}
