.o-back-link{
	display: block;
	position: relative;
	font-size: 1.2rem;
	padding-left: 1em;
	font-weight: 600;
	text-decoration: none;

	&__icon{
		position: absolute;
		top: calc(50% + 3px);
		left: 0;
		transform: translateY(-50%);
	}

	svg{
		stroke: currentColor;
		width: 8px;
		height: 12px;
	}
}
