.o-close{
	position: relative;
	width: 23px;
	height: 23px;

	&:before,
	&:after{
		content: '';
		display: block;
		width: 100%;
		height: 3px;
		background-color: currentColor;
		position: absolute;
		top: 50%;
		left: 50%;
	}

	&:before{
		transform: translate(-50%, -50%) rotate(45deg);
	}

	&:after{
		transform: translate(-50%, -50%) rotate(-45deg);
	}
}
