.c-slidein-tab{
	&__list{
		position: relative;
		height: 100%;

		> *{
			position: absolute;
			overflow: auto;
			top: 0;
			left: 0;
			height: 100%;
			width: 100%;
			background-color: #fff;
			opacity: 0;
			z-index: $nav-index + 5;

			&.active{
				opacity: 1;
				z-index: $nav-index + 7;
			}

			&.prev-active{
				opacity: 1;
				z-index: $nav-index + 6;
			}
		}

		&.animatable > *{
			transition: opacity $userOut $easeOut;
		}
	}

	&__title{
		font-size: 3rem;
		margin-bottom: 25px;
		color: $grey-light;
	}

	&__item-list{
		> *{
			&:not(:last-child){
				margin-bottom: 50px;
			}
		}
	}

	&__desc{
		font-weight: 1.4rem;
	}
}
