.c-testimonial{
	color: #fff;
	font-size: 1.6rem;

	> *{
		padding: 70px ($pull-in-gutter / 2) 80px;

		@media (min-width: $md){
			padding: 140px $pull-in-gutter 160px;
		}
	}

	&__author{
		line-height: 1.4;
		margin-top: 30px;

		> *{
			margin-bottom: 0;
		}
	}

	&__logo{
		display: flex;
		justify-content: center;
		margin-bottom: 30px;

		@media (min-width: $md){
			justify-content: flex-start;
			margin-bottom: 0;
		}
	}
}
