.c-service-list{
	&__item{
		padding-top: 40px;

		&:not(:last-child){
			border-bottom: 1px solid $black;
			padding-bottom: 40px;
		}

		@media (min-width: $md){
			padding-top: 75px;

			&:not(:last-child){
				padding-bottom: 75px;
			}
		}
	}

	&__sub-items{
		ol{
			margin-left: 0.75em;
		}
	}
}
