.c-scroll{
	position: relative;
	width: 100%;

	&.loaded{
		height: 100vh;
	}

	&.loaded &__viewport{
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		overflow: hidden;
	}

	&__track{
		width: 100%;

		> *{
			height: 100vh;
		}
	}

	.loaded &__track{
		> *{
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
		}
	}

	.loaded &__slide{
		overflow: hidden;
		z-index: 1;

		&.active{
			z-index: 3;
		}

		&.prev-active{
			z-index: 2;
		}
	}

	&__background{
		background-color: $primary;
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
	}

	&__slide:nth-child(2) &__background{
		background-color: $tertiary;
	}

	&__slide:nth-child(3) &__background{
		background-color: $secondary;
	}

	.loaded &__background{
		transition: opacity 1000ms $easeOut;
		opacity: 0;
	}

	.loaded &__slide.active &__background{
		opacity: 1;
	}

	&__container{
		position: absolute;
		top: 50%;
		left: 50%;
		max-width: $max-width;
		width: 100%;
		height: 100%;
		transform: translate(-50%, -50%);
		display: flex;
		flex-wrap: wrap;
		align-items: flex-start;

		@media (min-width: $md){
			align-items: center;
		}

		> *{
			flex: 1 1 100%;
			flex-basis: calc(100% - #{$full-gutter});
			margin-left: $gutter;
			margin-right: $gutter;

			&:first-child:not(:last-child){
				margin-top: 75px;
			}

			&:last-child:not(:first-child){
				margin-bottom: auto;
			}

			@media (min-width: $md){
				flex: 1 1 50%;
				flex-basis: calc(50% - #{$full-gutter});

				&:first-child:not(:last-child){
					margin-top: 0;
				}

				&:last-child:not(:first-child){
					margin-bottom: 0;
				}
			}
		}
	}

	&__fixed-container{
		position: absolute;
		left: 50%;
		top: 0;
		width: $max-width;
		height: 100vh;
		transform: translateX(-50%);
	}

	.loaded &__fixed-container{
		position: fixed;
		z-index: 20;
		pointer-events: none;
		height: 100%;
		max-width: $max-width;
		width: 100%;
		top: 50%;
		transform: translate(-50%, -50%);
	}

	&__logo{
		&-box{
			position: absolute;
			left: -2px;
			top: 84px;

			@media (min-width: $md){
				position: relative;
				left: 10px;
				top: 30px;
			}

			&,
			svg{
				width: 88px;
				height: 88px;

				@media (min-width: $md){
					width: 120px;
					height: 120px;
				}
			}

			svg{
				fill: #fff;
			}
		}

		&-q{
			position: absolute;
			top: 100px;
			left: 0;

			@media (min-width: $md){
				top: 50%;
				left: 22px;
				transform: translateY(calc(-50% + 35px));
			}

			svg{
				height: 55px;
				width: 67px;

				@media (min-width: $md){
					height: 67px;
					width: 87px;
				}
			}
		}

		&-container{
			display: flex;
			align-items: center;
		}

		&-text{
			margin-left: 75px;
			font-size: 6rem;
			height: 1em;

			@media (min-width: $md){
				font-size: 8.4rem;
				margin-left: 124px;
			}

			&--white{
				color: #fff;
			}
		}
	}

	&__slide{
		position: relative;
	}

	&__slide--first &__logo-bar{
		opacity: 0;
		transition: opacity 650ms $easeOut;
	}

	&__slide--first.prev-active &__logo-bar{
		opacity: 1;
		transition: opacity 350ms 650ms $easeOut;
	}

	.loaded &__slide &__logo-text{
		transform: translateY(-100vh);
		animation-duration: 1000ms;
		animation-fill-mode: forwards;
		animation-iteration-count: 1;
		animation-timing-function: $easeOut;
	}

	.down &__slide.active &__logo-text{
		animation-name: scrollUp;
	}

	.down &__slide.prev-active &__logo-text{
		animation-name: scrollUpAway;
	}

	.up &__slide.active &__logo-text{
		animation-name: scrollDown;
	}

	.up &__slide.prev-active &__logo-text{
		animation-name: scrollDownAway;
	}

	.loaded &__slide--first &__logo-text{
		opacity: 0;
		transform: none;
		animation: none;
		transition: opacity 1000ms $easeOut;
	}

	&__logo-box{
		opacity: 1;
		transition: opacity 1000ms $easeOut;
	}

	&__slide--first.active &__logo-text{
		opacity: 1;
	}

	&__logo-box.prev-active,
	&__slide--first.prev-active &__logo-text{
		opacity: 0;
	}

	&__title{
		font-size: 4rem;
		line-height: 1.4;
		color: #fff;
		text-transform: uppercase;
	}

	&__content{
		overflow: visible;
		font-size: 1.6rem;
		padding-top: 25px;

		> *:not(a){
			display: none;

			@media (min-height: 575px){
				display: block;
			}
		}

		@media (min-width: $md){
			height: 0;
		}

		&-container{
			margin-bottom: 55px;
		}
	}

	.loaded &__slide &__content-container{
		transform: translateY(-100vh);
		animation-duration: 1000ms;
		animation-fill-mode: forwards;
		animation-iteration-count: 1;
		animation-timing-function: $easeOut;
	}

	.loaded &__slide.active &__content-container{
		transform: none;
	}

	.down &__slide.active &__content-container{
		animation-name: scrollDown;
	}

	.down &__slide.prev-active &__content-container{
		animation-name: scrollDownAway;
	}

	.up &__slide.active &__content-container{
		animation-name: scrollUp;
	}

	.up &__slide.prev-active &__content-container{
		animation-name: scrollUpAway;
	}

	&__btn{
		margin-top: space(2);
	}

	&__dots{
		position: absolute;
		top: 50%;
		right: 22px;
		z-index: 8;
		transform: translateY(-50%);
		display: flex;
		flex-direction: column;
	}

	&__dot{
		position: relative;
		width: 16px;
		height: 16px;
		color: transparent;

		&:before{
			content: '';
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			width: 12px;
			height: 12px;
			border-radius: 50%;
			border: 2px solid #fff;
			transition: background-color $userOut $easeOut;
		}

		&.active:before{
			background-color: #fff;
		}
	}

	&__next-btn{
		display: none;
		position: relative;
		width: 48px;
		height: 48px;

		&-container{
			position: absolute;
			bottom: 40px;
			right: 10px;
			z-index: 8;
		}

		&-icon{
			position: absolute;
			top: 50%;
			left: 50%;
			width: 28px;
			height: 28px;
			transform: translate(-50%, -50%);

			svg{
				stroke: #fff;
				width: 28px;
				height: 28px;
			}
		}
	}

	.loaded &__next-btn{
		display: block;
	}
}
