.tmp-page-nav{
	&__container{
		position: fixed;
		bottom: 0;
		left: 0;
		background-color: #ccc;
		display: flex;
		flex-direction: column;
		padding: 25px;
		z-index: 20000;
		transform: translateX(-100%);
		transition: transform $userOut $easeOut;

		> *{
			color: #000;

			&:not(:last-child):not(:first-child){
				margin-bottom: 10px;
			}
		}

		.active &{
			transform: none;
		}
	}

	&__btn{
		position: absolute;
		bottom: 0;
		right: -100px;
		background-color: #ccc;
		font-weight: 700;
		width: 100px;
		height: 100px;
		margin-bottom: 0;
	}
}
