.o-quote{
	font-size: 1.8rem;
	font-style: normal;
	border: 0;
	padding: 0;

	&:first-child{
		margin-top: 45px;
	}

	&__content{
		margin-top: 50px;
		margin-bottom: 50px;
		position: relative;

		&:before,
		&:after{
			position: absolute;
			display: block;
			font-family: Georgia, serif;
			font-size: 8.5rem;
		}

		&:before{
			content: '\201C';
			top: 0;
			left: 0;
			transform: translateY(-50%);
		}

		&:after{
			content: '\201D';
			bottom: 0;
			right: 0;
			transform: translateY(80%);
		}
	}

	&__author{
		font-style: normal;

		> *{
			margin-bottom: 0;
		}
	}

	&__name{
		font-size: 1.6rem;
	}
}
