.c-cta{
	display: flex;
	justify-content: center;
	color: #fff;
	text-align: center;
	margin-top: 90px;
	margin-bottom: 90px;

	@media (min-width: $sm){
		margin-bottom: 115px;
	}

	&__container,
	&__title-link{
		display: flex;
		flex-direction: column;
		align-items: center;

		> *:not(:last-child){
			margin-bottom: 30px;
		}
	}

	&__container{
		max-width: 650px;
		width: 100%;

		> *{
			width: 100%;
		}
	}

	&__container:hover .o-wrapped-icon__wrapper,
	&__container:focus .o-wrapped-icon__wrapper{
		fill: $primary;
	}

	&__title-link{
		color: currentColor;
		text-decoration: none;

		&:hover,
		&:focus{
			color: currentColor;
		}
	}

	&__title{
		@include css-lock(2.4, 3, 560, 768, font-size, rem);
	}

	&__content,
	&__btn{
		display: none;

		@media (min-width: $sm){
			display: block;
		}
	}

	&__list{
		display: flex;

		> *{
			flex: 1 1 50%;
			flex-basis: calc(50% - #{$full-gutter});
			margin-left: $gutter;
			margin-right: $gutter;
			display: flex;
			justify-content: center;

			@media (min-width: $md){
				flex-basis: calc(50% - #{$full-gutter * 2});
				margin-left: $full-gutter;
				margin-right: $full-gutter;
			}
		}
	}
}
