.c-image{
	position: relative;
	overflow: hidden;
	background-size: cover;

	&:after,
	&:before{
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		opacity: 0;
		transition: opacity 1000ms $easeOut;
	}

	&:before{
		background-image: inherit;
		background-size: cover;
		filter: blur(10px);
	}

	&:after{
		background-color: rgba(0, 0, 0, 0.6);
	}

	&.fade:before,
	&.active:after{
		opacity: 1;
	}

	&__sizer{
		width: 100%;
		max-width: 1300px;

		&:before{
			content: '';
			display: block;
			height: 0;
			padding-bottom: ratio(1300, 623);
		}
	}

	&__container{
		position: absolute;
		max-width: $max-width;
		width: 100%;
		height: 100%;
		padding-left: $gutter;
		padding-right: $gutter;
		left: 50%;
		top: 0;
		z-index: 1;
		transform: translate(-50%, -50%);
		opacity: 0;
		transition: transform 1000ms $easeOut, opacity 1000ms $easeOut;
	}

	.active &__container{
		transition-delay: 250ms;
		transform: translate(-50%, 0);
		opacity: 1;
	}

	&__content{
		position: relative;
		height: 100%;
		width: 100%;
		color: #fff;
		font-size: 1.6rem;
		display: flex;
		flex-direction: column;
		justify-content: flex-end;
		padding-bottom: 25px;

		@media (min-width: $sm){
			font-size: 1.8rem;
		}

		@media (min-width: $md){
			width: 60%;
			font-size: 2.2rem;
			padding-bottom: 75px;
		}

		&-wrap{
			position: relative;
		}
	}

	&__logo{
		position: absolute;
		top: -10px;
		left: 10px;
		transform: translateY(-100%);
		fill: $primary;
		display: none;

		@media (min-width: unit(430 / 16, em)){
			display: block;
		}

		@media (min-width: $md){
			top: 10px;
			left: -20px;
			transform: translateX(-100%);
		}

		svg{
			width: 75px;
			height: 55px;
		}
	}
}
