.l-box-list{
	width: 100%;

	@media (min-width: #{unit(860 / 16, em)}){
		width: 75%;
	}

	&__container{
		display: flex;
		flex-wrap: wrap;
		margin-top: 50px;
		width: 100%;
		margin-left: 0;
		margin-right: 0;

		> *{
			flex: 1 1 100%;
			flex-basis: calc(100% - #{$full-gutter});
			margin-left: $gutter;
			margin-right: $gutter;
			margin-bottom: 35px;
			display: flex;
			flex-direction: column;
			align-items: center;

			@media (min-width: $xs){
				display: block;
				margin-bottom: 0;
				flex: 0 1 33.333%;
				flex-basis: calc(33.333% - #{$full-gutter});
			}

			> *{
				&:first-child{
					margin-bottom: 25px;

					@media (min-width: $xs){
						margin-bottom: 50px;
					}
				}
			}
		}
	}

	&--single &__container{
		margin-top: 0;
		display: flex;
		flex-direction: column;
		align-items: center;

		@media (min-width: $md){
			display: block;
		}

		> *{
			flex: 1 1 100%;
			flex-basis: calc(100% - #{$full-gutter});
			margin: 0;
		}
	}

	ul,
	ol{
		margin-left: 1em;
	}
}
