.o-icon-box{
	width: 140px;
	background-color: $secondary;
	color: #fff;
	text-align: center;
	font-size: 1.8rem;
	line-height: 1.4167;
	border-top-left-radius: $border-radius * 2;
	border-top-right-radius: $border-radius * 2;
	border-bottom-left-radius: $border-radius * 2;

	@media (min-width: $md){
		width: 165px;
		font-size: 2.2rem;
	}

	svg{
		fill: currentColor;
	}

	&--large{
		width: 260px;
	}

	&--light-grey{
		background-color: $grey-highlight;
		color: $black;
	}

	&--light-blue{
		background-color: $secondary-light;
	}

	&--small-text{
		font-size: 1.4rem;

		@media (min-width: $md){
			font-size: 1.8rem;
		}
	}

	&__container{
		position: relative;
		height: 0;
		padding-bottom: 100%;

		> *{
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			max-width: calc(100% - 20px);
			max-height: calc(100% - 20px);
			width: 100%;
		}
	}
}
