.c-story{
	&__image{
		position: relative;
		height: 0;
		padding-bottom: ratio(945, 480);
		margin-bottom: space(2);

		@media (min-width: $sm){
			margin-bottom: space(4);
		}

		@media (min-width: $md){
			margin-bottom: space(6);
		}

		img{
			position: absolute;
			top: 50%;
			left: 50%;
			height: auto;
			width: 100%;
			transform: translate(-50%, -50%);
		}
	}

	&--insight &__image{
		margin-bottom: 10px;
	}

	&__content{
		display: flex;
		flex-direction: column;
		align-items: flex-start;

		> *{
			&:not(:last-child){
				margin-bottom: space(2);

				@media (min-width: $sm){
					margin-bottom: 0;
				}
			}
		}

		@media (min-width: $sm){
			flex-direction: row;
			justify-content: space-between;
			align-items: flex-end;
		}
	}

	&__titles{
		display: flex;
		flex-direction: column;
	}

	&__title{
		color: $tertiary;
		font-size: 3rem;
	}

	&--insight &__title{
		font-size: 2.2rem;
	}

	&__subtitle{
		font-size: 2.2rem;
	}

	&--insight &__subtitle{
		font-size: 1.6rem;
	}

	&__list{
		> *{
			&:not(:last-child){
				margin-bottom: 60px;
			}
		}
	}

	&__link{
		position: relative;
		overflow: hidden;
		display: block;
	}

	&__insight-tag{
		position: absolute;
		top: 0;
		right: 0;

		&:before{
			content: '';
			position: absolute;
			z-index: 1;
			top: 0;
			right: 0;
			width: 0;
			height: 0;
			border-bottom: 126px solid transparent;
			border-left: 128px solid transparent;
			border-top: 126px solid $tertiary;
		}

		span{
			position: absolute;
			z-index: 2;
			top: 22px;
			right: 22px;
			color: #fff;
			font-size: 1.2rem;
		}
	}
}

@supports (object-fit: cover){
	.c-story{
		&__image{
			img{
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				object-fit: cover;
				transform: none;
			}
		}
	}
}
