.l-row{
	padding-top: 30px;
	padding-bottom: 60px;

	@media (min-width: $md){
		padding-top: 50px;
		padding-bottom: 100px;
	}

	&--large{
		padding-top: 60px;
		padding-bottom: 75px;

		@media (min-width: $md){
			padding-top: 100px;
			padding-bottom: 120px;
		}
	}

	&--grey{
		background-color: $grey;
	}

	&--black{
		background-color: $black;
	}

	&--orange{
		background-color: $primary;
	}

	&--blue{
		background-color: $secondary;
	}

	&--green{
		background-color: $tertiary;
	}
}
