.c-cross-slider{
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: center;
	padding-top: 20px;
	padding-bottom: 20px;

	@media (min-width: $md){
		flex-wrap: nowrap;
		padding-top: 120px;
		padding-bottom: 120px;
	}

	> *{
		margin-left: $gutter;
		margin-right: $gutter;
		flex: 1 1  100%;
		flex-basis: calc(100% - #{$full-gutter});

		@media (min-width: $md){
			flex: 1 1  50%;
			flex-basis: calc(50% - #{$full-gutter});
		}
	}

	&__container{
		position: relative;
		width: 100%;
		padding-left: $gutter;
		padding-right: $gutter;

		@media (min-width: $md){
			padding-left: 0;
			padding-right: 0;
		}
	}

	&__viewport{
		position: relative;
		overflow: hidden;
	}

	&__track{
		display: flex;

		> *{
			flex: 1 0 100%;
			width: 100%;
		}
	}

	&__slide{
		background-color: #fff;
	}

	&__title{
		font-size: 2.2rem;
		color: $grey-light;
		margin-bottom: 25px;
	}

	&__nav{
		margin-bottom: 30px;
		display: flex;
		flex-wrap: wrap;
		justify-content: center;

		> *{
			margin-bottom: 10px;
			margin-left: 5px;
			margin-right: 5px;
		}

		@media (min-width: $md){
			height: 450px;
			margin-bottom: 0;
			position: relative;
			display: block;

			> *{
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				margin: 0;

				&:nth-child(1){
					transform: translate(25%, -50%);
				}

				&:nth-child(2){
					transform: translate(-50%, 25%);
				}

				&:nth-child(3){
					transform: translate(-125%, -50%);
				}

				&:nth-child(4){
					transform: translate(-50%, -125%);
				}
			}
		}
	}

	&__btn{
		> *{
			background-color: transparentize($grey-light, 0.7);
			transition: background-color $userOut $easeOut;
		}

		&.active{
			> *{
				background-color: $primary;
			}
		}
	}
}
