.c-story-slider{
	width: calc(100% - #{$full-gutter * 2});
	margin-left: $full-gutter;
	margin-right: $full-gutter;
	margin-bottom: 25px;

	@media (min-width: $md){
		width: calc(100% - #{$pull-in-gutter * 2});
		margin-left: $pull-in-gutter;
		margin-right: $pull-in-gutter;
	}

	&__arrow{
		position: absolute;
		top: 0;
		height: calc(100% - 135px);

		svg{
			stroke: $black;
			width: 26px;
			height: 26px;
		}

		&--prev{
			left: -35px;

			@media (min-width: $md){
				left: -60px;
			}
		}

		&--next{
			right: -35px;

			@media (min-width: $md){
				right: -60px;
			}
		}
	}

	&__btn{
		position: absolute;
		bottom: 0;
		right: 0;
		transform: translateY(100%);

		@media (min-width: $md){
			transform: none;
		}
	}
}
