.l-enquiry{
	display: flex;
	flex-wrap: wrap;
	margin-top: 30px;
	margin-bottom: 30px;

	&__left,
	&__right{
		flex: 1 1 100%;
		flex-basis: calc(100% - #{$gutter});
		margin-bottom: 30px;

		@media (min-width: $md){
			flex: 1 1 50%;
			flex-basis: calc(50% - #{$gutter});
		}
	}

	&__left{
		margin-right: $gutter;
	}

	&__right{
		margin-left: $gutter;
		display: flex;

		> *{
			display: flex;
			flex: 1 1 100%;

			> *{
				flex: 1 1 100%;
			}
		}
	}

	&__btn{
		flex: 1 1 100%;
		padding-left: calc(50% + #{$gutter});
	}
}

@supports (display: grid){
	.l-enquiry{
		display: grid;
		grid-template-columns: 1fr;
		grid-template-areas:
			"left"
			"right"
			"btn";
		grid-gap: $gutter;

		@media (min-width: $md){
			grid-template-columns: repeat(2, 1fr);
			grid-template-areas:
				"left right"
				".... btn";
			grid-gap: $full-gutter;
		}

		&__left,
		&__right{
			margin-bottom: 0;
		}

		&__left{
			grid-area: left;
			margin-right: 0;
		}

		&__right{
			grid-area: right;
			display: flex;
			margin-left: 0;

			> *{
				display: flex;
				flex: 1 1 100%;

				> *{
					flex: 1 1 100%;
				}
			}
		}

		&__btn{
			grid-area: btn;
			padding-left: 0;
		}
	}
}
